@import '../../stylemodules/main.scss';

.ziple-service-page {
    main.service-category {
        padding:120px 0;
        @include responsive(TABLET) {
            padding: 110px 0;
        }

        @include responsive(MOBILE-LG) {
            padding: 100px 0;
        }

        @include responsive(MOBILE-MD) {
            padding: 80px 0;
        }

        .service-category-sub {
            font-size: $md-font-size-d;
            font-weight:600;
            color:$sub-color;
            padding-bottom:60px;
            @include responsive(TABLET) {
                font-size: $lg-font-size-m;
                padding-bottom:55px;
            }
    
            @include responsive(MOBILE-LG) {
                font-size: $lg-font-size-m;
                padding-bottom:45px;
            }
    
            @include responsive(MOBILE-MD) {
                font-size: $lg-font-size-m;
                padding-bottom:40px;
            }
        }
        .service-category-title {
            font-size: $lg-font-size-d;
            font-weight:600;
            color:$main-color-darnken;
            padding-bottom:100px;
            @include responsive(TABLET) {
                font-size: $lg-font-size-t;
                padding-bottom:70px;
            }
    
            @include responsive(MOBILE-LG) {
                font-size: $lg-font-size-m;
                padding-bottom:60px;
            }
    
            @include responsive(MOBILE-MD) {
                font-size: $lg-font-size-m;
                padding-bottom:50px;
            }
        }

        .service-category-list {
            width: 100%;
            display: flex;
            justify-content: space-around;
            flex-wrap: wrap;
            
            li {
                width:33.3%;
                margin-top:30px;
                
                @include responsive(TABLET) {
                    width:50%;
                }
        
                @include responsive(MOBILE-LG) {
                    width:50%;
                }
        
                @include responsive(MOBILE-MD) {
                    width:100%;
                }

                img {
                    padding-bottom:20px;
                }
                h6 {

                    padding-bottom:20px;
                    font-size:$sm-font-size-d;
                    font-weight:600;
                    color:#585858;

                }
                p {
                    color: $sub-color;
                    font-weight:800;
                }
            }
        }
    }
}