@import '../../stylemodules/main.scss';


.solution-background {

    // 메인 영상
    main.ziple-video {
        width: 100vw;
        height: 100vh;
        overflow: hidden;
        position: relative;

        @include responsive(TABLET) {
            height: 87vh;
        }

        @include responsive(MOBILE-LG) {
            height: 80vh;
        }

        p.ziple-main-title {
            position: absolute;
            z-index: 100;
            color: #ffffff;
            top: 200;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);

            h2 {
                text-align: left;
                font-size: 3rem;
                font-weight: 700;
                line-height: 60px;
                padding-bottom: 20px;

                @include responsive(TABLET) {
                    font-size: $lg-font-size-d;
                    line-height: 50px;
                }

                @include responsive(MOBILE-LG) {
                    font-size: $lg-font-size-t;
                    line-height: 40px;
                }

                @include responsive(MOBILE-LG) {
                    font-size: $lg-font-size-m;
                    line-height: 35px;
                }

            }
        }

        video {
            filter: blur(0.6px);
            width: 100vw;
            position: absolute;
            left: 0;
            transition: all 0.2s;
            height: 100%;
            margin: 0;
            object-fit: fill;

            @include responsive(TABLET) {
                height: 87%;
            }

            @include responsive(MOBILE-LG) {
                height: 80%;
            }

        }
    }

    // 자이플 솔루션 소개
    main.solution-info {
        padding: 120px 0;
        display: flex;

        @include responsive(TABLET) {
            flex-direction: column-reverse;
            padding: 110px 0;
        }

        @include responsive(MOBILE-LG) {
            padding: 90px 0;
        }

        @include responsive(MOBILE-LG) {
            padding: 80px 0;
        }

        .solution-info-txt {
            width: 50%;

            @include responsive(TABLET) {
                flex-direction: column-reverse;
                width: 100%;
            }

            text-align: left;

            p.solution-info-txt-title {

                h2 {
                    font-size: $lg-font-size-d;
                    font-weight: 700;
                    padding-bottom: 20px;

                    @include responsive(TABLET) {
                        text-align: center;
                        font-size: $lg-font-size-t;
                    }

                    @include responsive(MOBILE-LG) {
                        font-size: $lg-font-size-m;

                    }

                    @include responsive(MOBILE-LG) {
                        font-size: $md-font-size-m;

                    }
                }

                p.solution-info-txt-content {
                    font-size: $sm-font-size-m;
                    color: $sub-color;
                    letter-spacing: 0.4px;
                    line-height: 20px;

                    @include responsive(TABLET) {
                        text-align: center;
                    }


                }
            }

            div.solutuon-info-animation {
                @include responsive(TABLET) {
                    display: flex;
                    justify-content: space-around;
                }

                ul {
                    display: flex;
                    justify-content: space-between;
                    width: 375px;
                    padding: 0;
                    margin-top: 100px;
                    position: relative;
                    overflow: hidden;


                    li {
                        background: #ffffff;
                        z-index: 100;

                        p {
                            padding-top: 10px;
                            text-align: center;
                            font-size: $sm-font-size-m;

                        }

                        img {
                            z-index: 200;
                        }
                    }


                    .arrow_container {
                        position: absolute;
                        top: 30px;
                        display: block;
                        background: lightgray;
                        height: 1px;
                        width: 0px;


                        left: 0;

                        .arrow {
                            position: relative;
                            width: 0px;
                            background: lightgray;
                            height: 1px;
                            display: block;
                            transition: all 1s;

                        }

                        .arrow.active {
                            width: 300px;

                        }

                        .arrow::after {
                            position: absolute;
                            top: -3px;
                            right: 0;
                            content: '';
                            width: 6px;
                            height: 6px;
                            border-top: 1px solid lightgray;
                            border-right: 1px solid lightgray;
                            transform: rotate(45deg);
                        }
                    }
                }
            }

        }


        div.solution-info-img {
            width: 50%;

            @include responsive(TABLET) {
                flex-direction: column-reverse;
                width: 100%;
            }



            img {
                width: 100%;
                max-width: 440px;
                margin: 0 auto;
                display: block;
                padding-bottom: 20px;

                @include responsive(TABLET) {
                    width: 80%;

                }

                @include responsive(MOBILE-LG) {}

                @include responsive(MOBILE-MD) {}
            }
        }

    }

    // 자이플 팀메세지 
    main.solution-team-message {
        padding: 120px;
        background: #566CA5;

        @include responsive(TABLET) {
            padding: 110px 0;
        }

        @include responsive(MOBILE-LG) {
            padding: 100px 0;
        }

        @include responsive(MOBILE-MD) {
            padding: 80px 0;
        }

        div.solution-team-content {
            position: relative;
            padding: 100px 0;

            span.solution-team-img {
                position: absolute;
                width: 150px;
                height: 150px;

                @include responsive(TABLET) {
                    width: 120px;
                    height: 120px;

                }

                @include responsive(MOBILE-LG) {
                    width: 100px;
                    height: 100px;
                }

                @include responsive(MOBILE-MD) {}

            }

            span.solution-team-img:nth-child(1) {
                left: 0px;
                bottom: 0px;
                background: url('../../assets/images/Tools_perspective_matte.png') center center;
                background-size: cover;
            }

            span.solution-team-img:nth-child(2) {
                right: 0px;
                top: 0px;
                background: url('../../assets/images/Rocket_perspective_matte.png') center center;

                background-size: cover;
            }

            .solution-team-message-title {
                font-size: $lg-font-size-t;
                color: $main-color-lighten;
                font-weight: 600;
                padding-bottom: 30px;

                @include responsive(TABLET) {
                    font-size: $md-font-size-t;

                }

                @include responsive(MOBILE-LG) {
                    font-size: 1.2rem;
                }

                @include responsive(MOBILE-MD) {
                    font-size: $md-font-size-m;
                }
            }

            .solution-team-message-sub {
                font-size: $md-font-size-m;
                color: $main-color-lighten;
                font-weight: 300;
                padding-bottom: 30px;

                @include responsive(TABLET) {
                    font-size: $sm-font-size-t;

                }

                @include responsive(MOBILE-LG) {
                    font-size: $sm-font-size-m;
                }

                @include responsive(MOBILE-MD) {
                    font-size: $sm-font-size-m;
                }
            }

            .solution-team-message-stack {

                width: 40%;
                min-width: 325px;
                margin: 0 auto;
                display: flex;
                justify-content: space-around;
                flex-wrap:wrap;
                
                li {
                    padding: 8px 24px;
                    background: $event-color;
                    border-radius: 16px;
                    font-size: $lg-list-size;
                    color: $main-color-lighten;
                    margin-top:10px;
                    @include responsive(TABLET) {
                        padding: 6px 22px;
                        font-size: $lg-list-size;
                    }
    
                    @include responsive(MOBILE-LG) {
                        padding: 5px 20px;
                        font-size: $lg-list-size;
                    }
    
                    @include responsive(MOBILE-MD) {
                        padding: 6px 22px;
                        font-size: $md-list-size;
                    }
                }
            }
        }

    }
}